<template lang="html">
  <div class="login">
      <header>
          <button @click="back"></button>
          <span></span>
      </header>
      <div class="reg" @click="inReg"></div>
      <div class="login-list">
            <div class="phone">
                <input type="text" placeholder="" class="phone-num" ref="phoneNum" autofocus>
                <button class="get-yzm" ></button>
            </div>
            <div class="password">
                <input type="password" placeholder="" class="in-yzm" ref="pwd">
            </div>
            <button class="log" @click="log"></button>
            <p></p>
      </div>
      <div class="warn" v-show="isShow"></div>
      <div class="warn" v-show="wasShow"></div>      
  </div>
</template>
<script>
    import axios from 'axios';
    export default {
        data() {
            return {
                isShow : false
            }
        },
        methods: {  
            log(){
                if(/^1[34578][0-9]{9}$/.test(this.$refs.phoneNum.value)){
                    axios({
                        method: 'get',
                        url:`http://datainfo.duapp.com/shopdata/userinfo.php?status=login&userID=${this.$refs.phoneNum.value}&password=${this.$refs.pwd.value}`
                    }).then((res)=>{
                        switch(res.data){
                            case 0 : break;
                            case 2 : this.wasShow = true;
                                    setTimeout(function() {
                                        this.wasShow = false
                                    }.bind(this), 1500);break;
                            default: 
                            // this.$router.push({path: 'Mine',query:{isShow:true}});
                                    this.$router.push({name: 'Mine',params:{isShow:true}});
                                    bus.$emit('is-log', 1);
                                    sessionStorage.setItem('User',this.$refs.phoneNum.value)
                                    break;
                        }
                    });
                }else{
                    this.isShow = true;
                    setTimeout(function() {
                        this.isShow = false
                    }.bind(this), 1500);
                }
            },
            back(){
                history.go(-1)
            },
            inReg(){
                this.$router.push('Register');
            }
        }
    }
</script>
<style lang="scss">
    @import '../styles/login.scss';
</style>



